
import {Densities, MassOrDensity} from './../Constants/data';
export default{
  props:['label','id','material'],
  data () {
    return {
      materials: Object.keys(Densities) 
    }
  },
  methods:{
    updateSelectedMaterial: function($event){
      this.$emit('optionselected',$event.target.text);  
    }
  },
  created(){
  }
}



